<script>
//  Line component
import { Line } from 'vue3-chart-v2';

export default {
  extends: Line,
  // 
  props: {
    label: {
      type: String,
    },
    chartData: {
      type: Array,
    },
    options: {
      type: Object,
    },
    chartColorOptions: {
      type: Object,
    },
  },
  mounted() {
    const data = JSON.parse(JSON.stringify(this.chartData));
    //  Invalid Date
    data.forEach((item, index) => {
      if (item.date === 'Invalid Date') {
        data.splice(index, 1);
      }
    });
    // 
    const result = [];
    data.forEach((item) => {
      if (!this[item.date]) {
        this[item.date] = { date: item.date, total: 0 };
        result.push(this[item.date]);
      }
      this[item.date].total += item.total;
    }, Object.create(null));
    // ()
    const dates = result.map((d) => d.date).reverse();
    const totals = result.map((d) => d.total).reverse();

    this.renderChart(
      {
        //  x 
        labels: dates,
        // 
        datasets: [
          {
            // 
            label: this.label,
            data: totals,
            // 
            ...this.chartColorOptions,
          },
        ],
      },
      // 
      this.options,
    );
  },
};
</script>
